:root {
    --primary-color: #FFCC00;
    --primary-bg: #000;
}

.product-search-container{
    background-color: var(--primary-color);
}

.search-label{
    text-transform: uppercase;
    font-size: 20px;
}

.search-input{
    margin: 0;
    font-size: 18px;
    line-height: 18px;
    padding: 10px;
    border: 1px solid #ddd;
    background: #fff;
    border-radius: 6px;
    color: #888;
}