:root {
    --primary-color: #FFCC00;
    --secondary-color: #31c238;
    --white-color: #fff;
    --primary-bg: #000;
}

.bl-alert-text {
    padding: 0 6px; 
}

.bl-alert {
    position: fixed;
    /* z-index: 1050; */
    box-sizing: border-box;
    cursor: default;
    overflow: hidden;
    padding: 15px 20px;
    opacity: 1;
    transition: all 0.5s cubic-bezier(0.25, 0.8, 0.25, 1);
    left: 20px;
    right: 20px;
    border-radius: 30px;
    width: 90%;
    margin: auto;
}

.bl-alert .close {
    opacity: 1;
    text-shadow: none;
}

.bl-alert.sm{
    width: 260px !important;
}

.bl-alert.bottom{
    bottom: 0 !important;
}

.bl-alert.top{
    top: 25px !important;
}

.bl-alert.right{
    right: 55px !important;
}

.bl-alert button{
    right: 0 !important;
}

.alert-success {
    color: #000;
    background-color: var(--secondary-color);
    border-color: var(--secondary-color);
}

.alert-error {
    color: #000;
    background-color: red;
    border-color: red;
}