:root {
    --primary-color: #FFCC00;
    --primary-bg: #000;
}


.product-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
}

.product-item {
    border-radius: 13px;
    box-shadow: inset 0 0 10px #000000;
    background: #fff;
    padding: 20px;
    margin-bottom: 15px;
    display: table;
    flex-direction: column;
    align-items: center;
}

.prod-row {
    margin: 15px 0;
    text-align: center;
}

.prod-title {
    color: #000000;
    font-size: 18px;
    text-transform: capitalize;
}

.product-button{
    background-color: var(--primary-color);
    font-weight: bold;
    padding: 10px 35px;
    font-size: 16px;
    width: 90%;;
}