/* app css stylesheet */

html {
  width: 100%;
  height: 100%;
}

body {
  width: 100%;
  height: 100%;
}

html,
body {
  margin: 0px;
  padding: 0px;
}

body {
  font-family: 'Asap', sans-serif !important;
  font-size: 18px !important;
  margin: 0;
}

h1,h2,h3,h4,h5,h6,p {
  margin: 0;
}

.btn.active.focus,
.btn.active:focus,
.btn.focus,
.btn:active.focus,
.btn:active:focus,
.btn:focus {
  outline: none;
}

.form-control:focus {
  border-color: var(--primary-color);
  outline: 0;
  -webkit-box-shadow: inset 0 1px 1px rgba(0, 0, 0, .075), 0 0 15px var(--primary-color);
  box-shadow: inset 0 1px 1px rgba(0, 0, 0, .075), 0 0 15px var(--primary-color);
}

.nf-form-control-has-error{
  border-color: var(--red-color);
  -webkit-box-shadow: inset 0 1px 1px rgba(0, 0, 0, .075),
  0 0 15px var(--red-color);
  box-shadow: inset 0 1px 1px rgba(0, 0, 0, .075),
  0 0 15px var(--red-color);
}

.nf-form-control-has-error:focus {
  border-color: var(--red-color);
  -webkit-box-shadow: inset 0 1px 1px rgba(0, 0, 0, .075),
    0 0 15px var(--red-color);
  box-shadow: inset 0 1px 1px rgba(0, 0, 0, .075),
    0 0 15px var(--red-color);
}

.nf-error-block{
  color: var(--red-color);
}

.main-body {
  background: var(--primary-bg);
}

.section-title {
  padding: 35px 20px;
  text-align: center;
}

.section-title h2 {
  color: var(--primary-color);
  line-height: 1.28889;
  font-weight: 700;
  font-size: 45px;
}

.nf-row-selected-option {
  padding: 15px 0;
  border-bottom: 1px solid var(--primary-color);
  /*flex-wrap: wrap;*/
}

.nf-total-booking{
  background: var(--primary-color);
  color: black;
  padding: 15px;
}

.contact-map{
  background: url("../images/vg.PNG");
  background-position: center;
  background-size: cover;
}

.nf-booking-container .fa{
  margin-right: 5px;
}

.nf-booking-btn{
  background-color: var(--secondary-color);
  font-weight: bold;
  font-size: 20px;
  padding: 7px 30px;
  color: white;
}

.nf-price-row {
  margin-bottom: 10px;
}

.nf-accessory-label{
  font-weight: normal;
  margin-bottom: 0;
}

.nf-title-label{
  font-size: 20px;
  text-decoration: underline;
}

.nf-data-label{
  font-size: 17px;
}


/* break points */
/* Extra small devices (phones, 600px and down) */
@media only screen and (max-width: 600px) {
  body {
    font-size: 16px !important;
  }
  .menu-border {
    border-bottom: 0 !important;
  }
  .section-title h2 {
    font-size: 25px;
  }
  .control-container, .nf-booking-container {
    flex-direction: column;
  }
  .control-container div{
    width: 100% !important;
  }
  .nf-booking-container div{
    width: 100% !important;
  }
  .nf-accessory-container div{
    width: 100% !important;
  }
  .nf-detail-item div{
    width: 100% !important;
  }
  .nf-row-selected-option .nf-flex-40{
    width: 40% !important;
  }
}

/* Small devices (portrait tablets and large phones, 600px and up) */
@media only screen and (min-width: 600px) {
  
}

/* Medium devices (landscape tablets, 830px and up) */
@media only screen and (max-width: 830px) {

  .control-container, .nf-booking-container, .nf-price-container {
    flex-direction: column;
  }

  .nf-price-row, .nf-booking-btn-container {
    display: flex;
    justify-content: center;
  }

  .control-container div {
    width: 100%;
  }

  .nf-booking-container div {
    width: 100%;
  }

  .nf-detail-item div {
    width: 45%;
  }

  .nf-accessory-container div{
    width: 50%;
  }

  .nf-booking-form-container {
    padding-top: 18px;
  }

}

/* Large devices (laptops/desktops, 992px and up) */
@media only screen and (min-width: 831px) {
  .nf-booking-form-container {
    padding-left: 18px;
  }
  
}

/* Extra large devices (large laptops and desktops, 1200px and up) */
@media only screen and (min-width: 1200px) {
  .nf-price-row {
    margin-bottom: 0;
  }

  .nf-input{
    width: 95%;
  }

  .nf-booking-form-container {
    padding-left: 18px;
  }

  .nf-booking-form{
    display: flex;
    flex-wrap: wrap;
  }

  .nf-booking-form .form-group {
    /*width: 50%;*/
  }

}

@media only screen and (max-width: 1200px) {

.nf-booking-form div {
  width: 100%;
}

}

